var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"container"},[_c('h1',[_vm._v("Table of Contents")]),_vm._v(" "),_c('div',{staticStyle:{"margin-bottom":"50px"},domProps:{"innerHTML":_vm._s(_vm.tocHtml)}}),_vm._v(" "),_c('vue-markdown',{attrs:{"toc":true},on:{"toc-rendered":_vm.tocRenderered}},[_vm._v("\n# TV Series Visualizer \n\nThe TV Series Visualizer allows you to view episode ratings in a line or scatter chart, scaled by season.  \nMore details about a show is listed below, including a brief overview, airing dates and an extensive list of episodes.\n\n## 1 - Features\n\n  - Alternate between scatter and line chart by selecting the appropriate option above\n  - Zoom in the map by selecting an area via dragging, right click will reset\n  - All episodes are listed bottom right of the page, giving an image and appropriate season, episode numbers\n  - Details about the TV Series are bottom left, giving a poster image and more details about the selected series\n  - Click on the \"Episode Details\" panel to be taken to the homepage of the series\n  - Automatic filtering of Specials\n  - Search for a show via user input\n\n## 2 - Utilised APIs\n\nThe TV Series Visualized is based on information obtained from [TMDb][tmdb]\n\n| Endpoint | Documentation |\n| ------ | ------ |\n| /tv/{tv_id} | [3/tv/get-tv-details][tmdb_tv] |\n| /tv/{tv_id}/season/{season_number} |[3/tv-seasons/get-tv-season-details][tmdb_tv_s] |\n| /tv/top_rated  |[3/tv/get-top-rated-tv][tmdb_tv_tr] |\n| /tv/popular |[3/tv/get-popular-tv-shows][tmdb_tv_mp] |\n\nVisualizations are obtained from [Google Charts][gc]\n\n| Chart | Documentation |\n| ------ | ------ |\n| Line | [docs/gallery/linechart][gc_lc] |\n| Scatter | [docs/gallery/scatterchart][gc_sc] |\n| Combo | [docs/gallery/combochart][gc_cc] |\n\n## 3 - Tech\n\n* [VueJS] - The Progressive JavaScript Framework\n* [node.js] - a JavaScript runtime built on Chrome's V8 JavaScript engine\n* [jQuery] - The Write Less, Do More, JavaScript Library\n* [Jetbrains] - Developer Tools for Professionals and Teams\n* [GitHub] - The world's leading software development platform\n\n## 4 - Module Creation\n\nResponses from Top Rated or Most Popular\n\n```js\ndeclare module \"SeriesList\" {\n\n\texport interface Result {\n\t\tposter_path: string;\n\t\tpopularity: number;\n\t\tid: number;\n\t\tbackdrop_path: string;\n\t\tvote_average: number;\n\t\toverview: string;\n\t\tfirst_air_date: string;\n\t\torigin_country: string[];\n\t\tgenre_ids: number[];\n\t\toriginal_language: string;\n\t\tvote_count: number;\n\t\tname: string;\n\t\toriginal_name: string;\n\t}\n\n\texport interface RootObject {\n\t\tpage: number;\n\t\tresults: Result[];\n\t\ttotal_results: number;\n\t\ttotal_pages: number;\n\t}\n\t\n}\n```\n\nResponses from TVID\n\n```js\ndeclare module TVID {\n\n\texport interface CreatedBy {\n\t\tid: number;\n\t\tcredit_id: string;\n\t\tname: string;\n\t\tgender: number;\n\t\tprofile_path: string;\n\t}\n\n\texport interface Genre {\n\t\tid: number;\n\t\tname: string;\n\t}\n\n\texport interface LastEpisodeToAir {\n\t\tair_date: string;\n\t\tepisode_number: number;\n\t\tid: number;\n\t\tname: string;\n\t\toverview: string;\n\t\tproduction_code: string;\n\t\tseason_number: number;\n\t\tshow_id: number;\n\t\tstill_path: string;\n\t\tvote_average: number;\n\t\tvote_count: number;\n\t}\n\n\texport interface Network {\n\t\tname: string;\n\t\tid: number;\n\t\tlogo_path: string;\n\t\torigin_country: string;\n\t}\n\n\texport interface ProductionCompany {\n\t\tid: number;\n\t\tlogo_path: string;\n\t\tname: string;\n\t\torigin_country: string;\n\t}\n\n\texport interface Season {\n\t\tair_date: string;\n\t\tepisode_count: number;\n\t\tid: number;\n\t\tname: string;\n\t\toverview: string;\n\t\tposter_path: string;\n\t\tseason_number: number;\n\t}\n\n\texport interface RootObject {\n\t\tbackdrop_path: string;\n\t\tcreated_by: CreatedBy[];\n\t\tepisode_run_time: number[];\n\t\tfirst_air_date: string;\n\t\tgenres: Genre[];\n\t\thomepage: string;\n\t\tid: number;\n\t\tin_production: boolean;\n\t\tlanguages: string[];\n\t\tlast_air_date: string;\n\t\tlast_episode_to_air: LastEpisodeToAir;\n\t\tname: string;\n\t\tnext_episode_to_air?: any;\n\t\tnetworks: Network[];\n\t\tnumber_of_episodes: number;\n\t\tnumber_of_seasons: number;\n\t\torigin_country: string[];\n\t\toriginal_language: string;\n\t\toriginal_name: string;\n\t\toverview: string;\n\t\tpopularity: number;\n\t\tposter_path: string;\n\t\tproduction_companies: ProductionCompany[];\n\t\tseasons: Season[];\n\t\tstatus: string;\n\t\ttype: string;\n\t\tvote_average: number;\n\t\tvote_count: number;\n\t}\n}\n```\n\nResponses from TVID_Season\n\n```js\ndeclare module TVID_Season {\n\n\texport interface Crew {\n\t\tid: number;\n\t\tcredit_id: string;\n\t\tname: string;\n\t\tdepartment: string;\n\t\tjob: string;\n\t\tprofile_path: string;\n\t}\n\n\texport interface GuestStar {\n\t\tid: number;\n\t\tname: string;\n\t\tcredit_id: string;\n\t\tcharacter: string;\n\t\torder: number;\n\t\tprofile_path: string;\n\t}\n\n\texport interface Episode {\n\t\tair_date: string;\n\t\tcrew: Crew[];\n\t\tepisode_number: number;\n\t\tguest_stars: GuestStar[];\n\t\tname: string;\n\t\toverview: string;\n\t\tid: number;\n\t\tproduction_code: string;\n\t\tseason_number: number;\n\t\tstill_path: string;\n\t\tvote_average: number;\n\t\tvote_count: number;\n\t}\n\n\texport interface RootObject {\n\t\t_id: string;\n\t\tair_date: string;\n\t\tepisodes: Episode[];\n\t\tname: string;\n\t\toverview: string;\n\t\tid: number;\n\t\tposter_path: string;\n\t\tseason_number: number;\n\t}\n\n}\n\ndeclare module SeriesList {\n\n\texport interface Result {\n\t\tposter_path: string;\n\t\tpopularity: number;\n\t\tid: number;\n\t\tbackdrop_path: string;\n\t\tvote_average: number;\n\t\toverview: string;\n\t\tfirst_air_date: string;\n\t\torigin_country: string[];\n\t\tgenre_ids: number[];\n\t\toriginal_language: string;\n\t\tvote_count: number;\n\t\tname: string;\n\t\toriginal_name: string;\n\t}\n\n\texport interface RootObject {\n\t\tpage: number;\n\t\tresults: Result[];\n\t\ttotal_results: number;\n\t\ttotal_pages: number;\n\t}\n}\n```\n\n\n\n## 5 - Obtaining data from TMDb\n\nTop Rated or Most Popular\n\n```js\nvar settings = {\n  \"async\": true,\n  \"crossDomain\": true,\n  \"url\": \"https://api.themoviedb.org/3/tv/top_rated?page=1&language=en-US&api_key={api_key}\",\n  \"method\": \"GET\",\n  \"headers\": {},\n  \"data\": \"{}\"\n}\n\n$.ajax(settings).done(function (response) {\n    // seriestlist: SeriesList.RootObject\n    seriesList = response;\n  });\n```\n\nTV Information\n\n```js\nvar settings = {\n  \"async\": true,\n  \"crossDomain\": true,\n  \"url\": \"https://api.themoviedb.org/3/tv/{tvid}?language=en-US&api_key={api_key}\",\n  \"method\": \"GET\",\n  \"headers\": {},\n  \"data\": \"{}\"\n}\n\n$.ajax(settings).done(function (response) {\n  // tvInformation = TVID.RootObject\n  tvInformation = response;\n});\n```\n\nTV Season Information\n\n```js\nvar settings = {\n  \"async\": true,\n  \"crossDomain\": true,\n  \"url\": \"https://api.themoviedb.org/3/tv/{tvid}/season/{season_number}?language=en-US&api_key={api_key}\",\n  \"method\": \"GET\",\n  \"headers\": {},\n  \"data\": \"{}\"\n}\n\n$.ajax(settings).done(function (response) {\n  // seasonInformation = TVID_Season.RootObject\n  seasonInformation = response;\n});\n```\n\n## 6 - Visualizing Data using Google Charts\n\n### 6.1 -  Create a function to draw\n\nReference the chart loader in script tag\n\n```html\n<script src=\"https://www.gstatic.com/charts/loader.js\"></script>\n```\n\nCreate a DataTable\n```js\nlet data = new google.visualization.DataTable();\n```\n\nAdd appropriate Columns - [API Refernece][gc_r]\n\n```js\ndata.addColumn(type, opt_label, opt_id)\ndata.addColumn(description_object)\n```\n\nAdd your data -  [API Refernece][gc_r]\n\n```js\n// numOrArray = the data you've collected\ndata.addRows(numOrArray)\n```\n\nSpecify your options\n```js\n// these are the option the TV Series Visualizer uses\nlet options = {\n\ttitle: title,\n\theight: 500,\n\tchartArea: {'width' : '100%', 'height' : '80%', 'left' : '40', },\n\tlegend: {'position': 'bottom' as 'bottom'},\n\tseriesType: $this.sType,\n    explorer: {\n\t\tactions: ['dragToZoom', 'rightClickToReset'],\n\t\taxis: 'horizontal',\n\t\tkeepInBounds: true,\n\t\tmaxZoomIn: 7.0\n\t} as google.visualization.ComboChartOptions\n```\n\nVisualizing data using Google Charts\n\n```js\n// element = where the graph will be placed\nlet chart = new google.visualization.ComboChart(element);\nlet chart = new google.visualization.LineChart(element);\nlet chart = new google.visualization.ScatterChart(element);\n```\n\nDraw the chart\n\n```js\nchart.draw(data, options);\n```\n\n#### 6.2 - Set for your chart to load\n\nLoad packages\n\n```js\ngoogle.charts.load('current', {\n\tpackages: ['corechart', 'line']\n});\n```\n\nSet the onLoadCallback\n\n```js\ngoogle.setOnLoadCallback(function() {\n    draw(hasSpecials, seasonCount, showData, title);\n});\n```\n\n## 7 - Todos\n\n - Use Combo Chart to implement trendlines\n - Add options for viewing individual seasons\n - Integrate ratings from other sources\n\n\n\n[//]: # (These are reference links used in the body of this note and get stripped out when the markdown processor does its job. There is no need to format nicely because it shouldn't be seen. Thanks SO - http://stackoverflow.com/questions/4823468/store-comments-in-markdown-syntax)\n\n   [gc]:"),_vm._v("//developers.google.com/chart/>\n   [tmdb]: "),_vm._v("//www.themoviedb.org/documentation/api>\n   [tmdb_tv]: "),_vm._v("//developers.themoviedb.org/3/tv/get-tv-details>\n   [tmdb_tv_s]: "),_vm._v("//developers.themoviedb.org/3/tv-seasons/get-tv-season-details>\n   [tmdb_tv_mp]: "),_vm._v("//developers.themoviedb.org/3/tv/get-popular-tv-shows>\n   [tmdb_tv_tr]: "),_vm._v("//developers.themoviedb.org/3/tv/get-top-rated-tv>\n   [gc_cc]: "),_vm._v("//developers.google.com/chart/interactive/docs/gallery/combochart>\n   [gc_sc]: "),_vm._v("//developers.google.com/chart/interactive/docs/gallery/scatterchart>\n   [gc_lc]: "),_vm._v("//developers.google.com/chart/interactive/docs/gallery/linechart>\n   [VueJS]: "),_vm._v("//vuejs.org/>\n   [node.js]: "),_vm._v("//nodejs.org>\n   [jQuery]: "),_vm._v("//jquery.com>\n   [Jetbrains]: "),_vm._v("//www.jetbrains.com/>\n   [gc_r]: "),_vm._v("//developers.google.com/chart/interactive/docs/reference>\n   [github]: "),_vm._v("//github.com/>\n\n\n")])],1)}
